import React, { useState, useEffect } from "react";
import { Modal, ModalHeader, ModalBody, ModalFooter, Button, FormGroup, Label, Input, Form } from "reactstrap";
import bcrypt from "bcryptjs"; // Import th vin bcryptjs

const EditUserModal = ({ open, onClose, userToEdit, onUpdateUser, baseUrlAuth }) => {
  const [user, setUser] = useState({
    username: "",
    password: "",
    email: "",
    fullName: "",
    phone: "",
  });

  useEffect(() => {
    if (userToEdit) {
      setUser(userToEdit);
    }
  }, [userToEdit]);

  const handleChange = (event) => {
    const { name, value } = event.target;
    setUser((prevUser) => ({ ...prevUser, [name]: value }));
  };

  const handleUpdateUser = () => {

       // Kim tra iu kin trc khi thm ngi dng
    if (!user.username || !user.password || !user.email || !user.fullName || !user.phone) {
      alert("Vui lng in y  thng tin ngi dng.");
      return;
    }

    // Kim tra email hp l s dng Regular Expression
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(user.email)) {
      alert("Vui lng nhp ng nh dng email.");
      return;
    }

    // Kim tra h v tn khng cha s s dng Regular Expression
    const nameRegex = /^[^\d]+$/;
    if (!nameRegex.test(user.fullName)) {
      alert("H v tn khng c cha s.");
      return;
    }

    // Kim tra s in thoi hp l s dng Regular Expression
    const phoneRegex = /^\d{10}$/;
    if (!phoneRegex.test(user.phone)) {
      alert("Vui lng nhp s in thoi hp l (10 ch s).");
      return;
    }
    onUpdateUser(user);
    onClose();
  };

  return (
    <Modal isOpen={open} toggle={onClose}>
      <ModalHeader toggle={onClose}>Chnh Sa</ModalHeader>
      <ModalBody>
        <Form>
          <FormGroup>
            <Label for="username">Username</Label>
            <Input
              type="text"
              name="username"
              value={user.username}
              onChange={handleChange}
              style={{ borderRadius: "0.25rem" }}
            />
          </FormGroup>
          <FormGroup>
            <Label for="password">Password</Label>
            <Input
              type="password"
              name="password"
              value={user.password}
              onChange={handleChange}
              style={{ borderRadius: "0.25rem" }}
            />
          </FormGroup>
          <FormGroup>
            <Label for="email">Email</Label>
            <Input
              type="email"
              name="email"
              value={user.email}
              onChange={handleChange}
              style={{ borderRadius: "0.25rem" }}
            />
          </FormGroup>
          <FormGroup>
            <Label for="fullName">Full Name</Label>
            <Input
              type="text"
              name="fullName"
              value={user.fullName}
              onChange={handleChange}
              style={{ borderRadius: "0.25rem" }}
            />
          </FormGroup>
          <FormGroup>
            <Label for="phone">Phone</Label>
            <Input
              type="text"
              name="phone"
              value={user.phone}
              onChange={handleChange}
              style={{ borderRadius: "0.25rem" }}
            />
          </FormGroup>
        </Form>
      </ModalBody>
      <ModalFooter>
        <Button color="secondary" onClick={onClose} style={{ borderRadius: "0.25rem" }}>
          Cancel
        </Button>
        <Button color="primary" onClick={handleUpdateUser} style={{ borderRadius: "0.25rem" }}>
          Save
        </Button>
      </ModalFooter>
    </Modal>
  );
};

export default EditUserModal;
