const formatVND = (totalAmount) => {
    if (totalAmount === undefined || isNaN(totalAmount)) {
      return '0 VND';
    }
  
    // nh dng s tin thnh chui VND
    return new Intl.NumberFormat('vi-VN', { style: 'currency', currency: 'VND' }).format(totalAmount);
  };
  
  export { formatVND };