import React, { useState } from "react";
import {
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Input,
  Button,
  FormGroup,
  Label,
  Form,
} from "reactstrap";

const AddUserModal = ({ open, onClose, onAddUser }) => {
  const [user, setUser] = useState({
    username: "",
    password: "",
    email: "",
    fullName: "",
    phone: "",
  });

  const handleChange = (event) => {
    const { name, value } = event.target;
    setUser((prevUser) => ({ ...prevUser, [name]: value }));
  };

  const handleAddUser = () => {
    // Kim tra iu kin trc khi thm ngi dng
    if (!user.username || !user.password || !user.email || !user.fullName || !user.phone) {
      alert("Vui lng in y  thng tin ngi dng.");
      return;
    }

    // Kim tra email hp l s dng Regular Expression
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(user.email)) {
      alert("Vui lng nhp ng nh dng email.");
      return;
    }

    // Kim tra h v tn khng cha s s dng Regular Expression
    const nameRegex = /^[^\d]+$/;
    if (!nameRegex.test(user.fullName)) {
      alert("H v tn khng c cha s.");
      return;
    }

    // Kim tra s in thoi hp l s dng Regular Expression
    const phoneRegex = /^\d{10}$/;
    if (!phoneRegex.test(user.phone)) {
      alert("Vui lng nhp s in thoi hp l (10 ch s).");
      return;
    }

    // Thm ngi dng mi
    onAddUser(user);

    // Reset form v ng modal
    setUser({
      username: "",
      password: "",
      email: "",
      fullName: "",
      phone: "",
    });
    onClose();
  };

  return (
    <Modal isOpen={open} toggle={onClose}>
      <ModalHeader toggle={onClose}>Thm Mi </ModalHeader>
      <ModalBody>
        <Form>
          <FormGroup>
            <Label for="username">Username</Label>
            <Input
              type="text"
              name="username"
              value={user.username}
              onChange={handleChange}
            />
          </FormGroup>
          <FormGroup>
            <Label for="password">Password</Label>
            <Input
              type="password"
              name="password"
              value={user.password}
              onChange={handleChange}
            />
          </FormGroup>
          <FormGroup>
            <Label for="email">Email</Label>
            <Input
              type="email"
              name="email"
              value={user.email}
              onChange={handleChange}
            />
          </FormGroup>
          <FormGroup>
            <Label for="fullName">Full Name</Label>
            <Input
              type="text"
              name="fullName"
              value={user.fullName}
              onChange={handleChange}
            />
          </FormGroup>
          <FormGroup>
            <Label for="phone">Phone</Label>
            <Input
              type="text"
              name="phone"
              value={user.phone}
              onChange={handleChange}
            />
          </FormGroup>
        </Form>
      </ModalBody>
      <ModalFooter>
        <Button color="secondary" onClick={onClose}>
          thot
        </Button>
        <Button color="primary" onClick={handleAddUser}>
          Thm mi
        </Button>
      </ModalFooter>
    </Modal>
  );
};

export default AddUserModal;
  