import React, { useState, useEffect , useContext} from "react";
import {
  Container,
  Row,
  Col,
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Form,
  FormGroup,
  Label,
  Input,
} from "reactstrap";
import BookingTable from "./BookingTable";
import TourInfoModal from "./TourInfoModal";
import { AuthContext } from "../../../context/AuthContext";
import {PaymentStatus } from "../../utils/PaymentStatus"
const baseUrl = "http://localhost:8888/bookings";


const BookingList = () => {
  const [bookings, setBookings] = useState([]);
  const [loading, setLoading] = useState(false);
  const [openEditModal, setOpenEditModal] = useState(false);
  const [bookingToEdit, setBookingToEdit] = useState(null);
 const { token } = useContext(AuthContext);
  const [selectedTour, setSelectedTour] = useState(null);
  const [openTourModal, setOpenTourModal] = useState(false);

  useEffect(() => {
    fetchBookings();
  }, []);

  const fetchBookings = () => {
    setLoading(true);
    fetch(`${baseUrl}/get-all`, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
    })
      .then((response) => response.json())
      .then((data) => {
        setBookings(data);
      })
      .catch((error) => {
        console.error(error);
      })
      .finally(() => {
        setLoading(false);
      });
  };

  const handleEditBooking = () => {
    fetch(`${baseUrl}/update/${bookingToEdit.bookingId}`, {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify({
        fullName: bookingToEdit.fullName,
        email: bookingToEdit.email,
        phoneNumber: bookingToEdit.phoneNumber,
        address: bookingToEdit.address,
        note: bookingToEdit.note,
        guestSize: bookingToEdit.guestSize,
        tour: bookingToEdit.tour.id,
        totalAmount:bookingToEdit.totalAmount,
        status: bookingToEdit.status
      }),
    })
      .then((response) => {
        if (response.ok) {
          alert(" Sa Thnh cng");
          fetchBookings();
        } else {
          throw new Error("Khng th cp nht booking.");
        }
      })
      .catch((error) => {
        console.error(error);
      })
      .finally(() => {
        setOpenEditModal(false);
      });
  };

  const handleDeleteBooking = (bookingId) => {
    fetch(`${baseUrl}/delete/${bookingId}`, {
      method: "DELETE",
      headers: {
        Authorization: `Bearer ${token}`,
      },
    })
      .then((response) => {
        if (response.ok) {
          alert("Xa thnh cng ");
          fetchBookings();
        } else {
          throw new Error("Khng th xa booking.");
        }
      })
      .catch((error) => {
        console.error(error);
      });
  };

  const handleEditModalOpen = (booking) => {
    setBookingToEdit(booking);
    setOpenEditModal(true);
  };

  const handleShowTourInfo = (tour) => {
    console.log(tour);
    setSelectedTour(tour);
    setOpenTourModal(true);
  };

  const handleCloseModal = () => {
    setOpenEditModal(false);
  };

  return (
    <section>
      <Container>
        <Row>
          <Col>
            <h4 className="text-center">Danh sch booking</h4>
          </Col>
        </Row>
        <Row>
          <Col xs={12} md={3}></Col>
        </Row>
        <Row>
          <Col>
            <BookingTable
              bookings={bookings}
              loading={loading}
              handleEditModalOpen={handleEditModalOpen}
              handleDeleteBooking={handleDeleteBooking}
              handleShowTourInfo={handleShowTourInfo}
            />
          </Col>
        </Row>
      </Container>

      {/* Modal Sa */}
      <Modal isOpen={openEditModal} toggle={handleCloseModal}>
        <ModalHeader toggle={handleCloseModal}>Chnh sa booking</ModalHeader>
        <ModalBody>
          <Form>
            <FormGroup>
              <Label for="fullName">H tn</Label>
              <Input
                type="text"
                name="fullName"
                id="fullName"
                value={bookingToEdit?.fullName || ""}
                onChange={(e) =>
                  setBookingToEdit((prevBooking) => ({
                    ...prevBooking,
                    fullName: e.target.value,
                  }))
                }
              />
            </FormGroup>
            <FormGroup>
              <Label for="email">Email</Label>
              <Input
                type="email"
                name="email"
                id="email"
                value={bookingToEdit?.email || ""}
                onChange={(e) =>
                  setBookingToEdit((prevBooking) => ({
                    ...prevBooking,
                    email: e.target.value,
                  }))
                }
              />
            </FormGroup>
            <FormGroup>
              <Label for="phoneNumber">S in thoi</Label>
              <Input
                type="text"
                name="phoneNumber"
                id="phoneNumber"
                value={bookingToEdit?.phoneNumber || ""}
                onChange={(e) =>
                  setBookingToEdit((prevBooking) => ({
                    ...prevBooking,
                    phoneNumber: e.target.value,
                  }))
                }
              />
            </FormGroup>
            <FormGroup>
              <Label for="address">a ch</Label>
              <Input
                type="text"
                name="address"
                id="address"
                value={bookingToEdit?.address || ""}
                onChange={(e) =>
                  setBookingToEdit((prevBooking) => ({
                    ...prevBooking,
                    address: e.target.value,
                  }))
                }
              />
            </FormGroup>
            <FormGroup>
              <Label for="note">Ghi ch</Label>
              <Input
                type="textarea"
                name="note"
                id="note"
                value={bookingToEdit?.note || ""}
                onChange={(e) =>
                  setBookingToEdit((prevBooking) => ({
                    ...prevBooking,
                    note: e.target.value,
                  }))
                }
              />
            </FormGroup>
            <FormGroup>
              <Label for="guestSize">S lng khch</Label>
              <Input
                type="number"
                name="guestSize"
                id="guestSize"
                value={bookingToEdit?.guestSize || ""}
                onChange={(e) =>
                  setBookingToEdit((prevBooking) => ({
                    ...prevBooking,
                    guestSize: e.target.value,
                  }))
                }
              />
            </FormGroup>
          
            <FormGroup>
              <Label for="tour">M Tour</Label>
              <Input
                type="text"
                name="tour"
                id="tour"
                value={bookingToEdit?.tour.id || ""}
                onChange={(e) =>
                  setBookingToEdit((prevBooking) => ({
                    ...prevBooking,
                    tour: { id: e.target.value },
                  }))
                }
              />
            </FormGroup>
            <FormGroup>
              <Label for="guestSize">S tin </Label>
              <Input
                type="text"
                name="totalAmount"
                id="guestSize"
                value={bookingToEdit?.totalAmount || ""}
                onChange={(e) =>
                  setBookingToEdit((prevBooking) => ({
                    ...prevBooking,
                    totalAmount: e.target.value,
                  }))
                }
              />
            </FormGroup>
            <FormGroup>
        <Label for="status">Trng thi thanh ton</Label>
        <Input
          type="select"
          id="status"
          name="status"
          value={bookingToEdit?.status || ""}
          onChange={(e) =>
            setBookingToEdit((prevBooking) => ({
              ...prevBooking,
              status: e.target.value,
            }))
          }
        >
          <option value={PaymentStatus.PENDING}>PENDING</option>
          <option value={PaymentStatus.SUCCESSFUL}>SUCCESSFUL</option>
          <option value={PaymentStatus.FAILED}>FAILED</option>
        </Input>
      </FormGroup>
          </Form>
        </ModalBody>
        <ModalFooter>
          <Button color="primary" onClick={handleEditBooking}>
            Lu thay i
          </Button>{" "}
          <Button color="secondary" onClick={handleCloseModal}>
            Hy
          </Button>
        </ModalFooter>
      </Modal>

      <TourInfoModal
        open={openTourModal}
        handleClose={() => setOpenTourModal(false)}
        selectedTour={selectedTour}
      />
    </section>
  );
};

export default BookingList;
