import React, { useState, useEffect,useContext } from "react";
import { Table, Button, Modal, ModalHeader, ModalBody, Container,Pagination,} from "reactstrap";
import "./../../styles/tour-List.css";
import AddTourModal from "./AddTourModal";
import EditTourModal from "./EditTourModal";
import { AuthContext } from "../../../context/AuthContext"
import { EditorState, convertFromRaw ,convertFromHTML,ContentState} from "draft-js";
import { Editor } from "react-draft-wysiwyg";
import "react-draft-wysiwyg/dist/react-draft-wysiwyg.css";




const baseUrl = "http://localhost:8888/tours";

const TourList = () => {
  const [tours, setTours] = useState([]);
  const [selectedTour, setSelectedTour] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [modalOpen, setModalOpen] = useState(false);
  const [addModalOpen, setAddModalOpen] = useState(false);
  const [editModalOpen, setEditModalOpen] = useState(false);
  const {token} = useContext(AuthContext);



  useEffect(() => {
    fetchAllTours();
  }, []);

  const fetchAllTours = () => {
    setLoading(true);
    fetch(`${baseUrl}/get-all`, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        
      },
    })
      .then((response) => {
        if (!response.ok) {
          throw new Error("khng c tour no c hin th ");
        }
        return response.json();
      })
      .then((data) => {
        setTours(data);
        console.log(data);
      })
      .catch((error) => {
        setError(error);
      })
      .finally(() => {
        setLoading(false);
      });
  };

  const toggleModal = () => {
    setModalOpen(!modalOpen);
  };

  const handleTourClick = (tour) => {
    setSelectedTour(tour);
    toggleModal();
  };

  const toggleAddModal = () => {
    setAddModalOpen(!addModalOpen);
  };

  const toggleEditModal = () => {
    setEditModalOpen(!editModalOpen);
  };

  const [currentPage, setCurrentPage] = useState(1);
  const toursPerPage = 4;

  const indexOfLastTour = currentPage * toursPerPage;
  const indexOfFirstTour = indexOfLastTour - toursPerPage;
  const currentTours = tours.slice(indexOfFirstTour, indexOfLastTour);

  const paginate = (pageNumber) => {
    setCurrentPage(pageNumber);
  };


 
  const handleAddTour = (newTour) => {
    fetch(`${baseUrl}/create`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify(newTour),
    })
      .then((response) => {
        if (response.ok) {
          alert("Tour  c thm thnh cng");
          fetchAllTours();
        } else {
          throw new Error("Khng th thm tour.");
        }
      })
      .catch((error) => {
        console.error(error);
        setError("C li xy ra khi thm tour.");
      })
      .finally(() => {
        setAddModalOpen(false);
      });
  };

  const handleUpdateTour = (updatedTour) => {
    fetch(`${baseUrl}/update/${selectedTour.id}`, {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify(updatedTour),
    })
      .then((response) => {
        if (response.ok) {
          alert("Tour  c cp nht thnh cng");
          fetchAllTours();
        } else {
          throw new Error("Khng th cp nht tour.");
        }
      })
      .catch((error) => {
        console.error(error);
        setError("C li xy ra khi cp nht tour.");
      })
      .finally(() => {
        setSelectedTour(null);
        setEditModalOpen(false);
      });
  };

  const handleDeleteTour = () => {
    fetch(`${baseUrl}/${selectedTour.id}`, {
      method: "DELETE",
      headers: {
        Authorization: `Bearer ${token}`,
      },
    })
      .then((response) => {
        if (response.ok) {
          alert("Tour  c xa thnh cng");
          fetchAllTours();
        } else {
          throw new Error("Khng th xa tour.");
        }
      })
      .catch((error) => {
        console.error(error);
        setError("C li xy ra khi xa tour.");
      })
      .finally(() => {
        setSelectedTour(null);
      });
  };



  return (
    <Container>
      <section>
        <h4 className="text-center">Danh sch tours</h4>
        <Button color="primary" onClick={toggleAddModal}>Thm Tour</Button>
        {loading ? (
          <p>Loading...</p>
        ) : error ? (
          <p> xy ra li: {error.message}</p>
        ) : (
          <Table className="tour-table">
            <thead>
              <tr>
                <th>ID</th>
                <th>Tiu </th>
                <th>Thnh ph</th>
                <th>a ch</th>
                <th>Khong cch</th>
                <th>Hnh nh</th>
                <th>M t</th>
                <th>Gi</th>
                <th>S Ngi</th>
                <th>Ni bt</th>
                <th>nh gi</th>
                <th>Chc nng</th>
              </tr>
            </thead>
            <tbody>
              {currentTours.map((tour) => (
                <tr key={tour.id}  >
                  <td>{tour.id}</td>
                  <td>{tour.title}</td>
                  <td>{tour.city}</td>
                  <td>{tour.address}</td>
                  <td>{tour.distance}</td>
                  <td>
                    <img
                      src={tour.photo}
                      alt={tour.title}
                      style={{ maxWidth: "100px", borderRadius: "5px" }}
                    />
                  </td>
                  <td>
                    {tour.desc.length > 150 ? (
                      <div>
                        <p>{tour.desc.substring(0, 150)}...</p>
                        <Button color="link">Xem thm</Button>
                      </div>
                    ) : (
                      <div dangerouslySetInnerHTML={{ __html: tour.desc }} />
                    )}
                  </td>
                  <td>{tour.price}</td>
                  <td>{tour.maxGroupSize}</td>
                  <td>{tour.featured ? "C" : "Khng"}</td>
                  <td>
                    {tour.reviews.map((review) => (
                      <div key={review.id}>
                        <p>Tn ngi dng: {review.username}</p>
                        <p>nh gi: {review.rating}*</p>
                        <p>nh gi vn bn: {review.reviewText}</p>
                      </div>
                    ))}
                  </td>
                  <td>
                    <Button color="primary" onClick={() => handleTourClick(tour)}>Xem</Button>{" "}
                    <Button color="warning" onClick={() => { setSelectedTour(tour); toggleEditModal(); }}>Sa</Button>{" "}
                    <Button color="danger" onClick={() => { setSelectedTour(tour); handleDeleteTour(); }}>Xa</Button>
                  </td>
                </tr>
              ))}
            </tbody>
          </Table>
        )}
        <Pagination className="pagination">
        <div className="pagination">
        {Array.from({ length: Math.ceil(tours.length / toursPerPage) }, (_, index) => (
          <Button
            key={index}
            className={`page-item ${currentPage === index + 1 ? "active" : ""}`}
            onClick={() => paginate(index + 1)}
          >
            {index + 1}
          </Button>
        ))}
      </div>
        </Pagination>
      </section>
      <Modal isOpen={modalOpen} toggle={toggleModal} size="lg">
  <ModalHeader toggle={toggleModal}>{selectedTour?.title}</ModalHeader>
  <ModalBody>
  <td>
  {selectedTour && (
    <div>
      <Editor
        editorState={EditorState.createWithContent(ContentState.createFromBlockArray(convertFromHTML(selectedTour.desc)))}
        readOnly={true}
      />
    </div>
  )}
</td>
  </ModalBody>
</Modal>

      <AddTourModal isOpen={addModalOpen} toggleModal={toggleAddModal} handleAddTour={handleAddTour} />
      <EditTourModal isOpen={editModalOpen} toggleModal={toggleEditModal} selectedTour={selectedTour} handleUpdateTour={handleUpdateTour} />
    </Container>
  );
};

export default TourList;
