import React, { useState } from "react";
import { Modal, ModalHeader, ModalBody, Button, Form, FormGroup, Label, Input } from "reactstrap";
import { EditorState, ContentState, convertToRaw } from "draft-js";
import { Editor } from "react-draft-wysiwyg";
import { stateToHTML } from "draft-js-export-html";

import "react-draft-wysiwyg/dist/react-draft-wysiwyg.css";

const AddTourModal = ({ isOpen, toggleModal, handleAddTour }) => {
  const [editorState, setEditorState] = useState(EditorState.createEmpty());
  const [formData, setFormData] = useState({
    title: "",
    city: "",
    address: "",
    distance: "",
    photo: "",
    desc: "",
    price: "",
    maxGroupSize: "",
    featured: "",
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({ ...prevData, [name]: value }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const contentState = editorState.getCurrentContent();
    const rawContent = convertToRaw(contentState);
    const plainText = rawContent.blocks.map(block => block.text).join('\n');
    const htmlContent = stateToHTML(ContentState.createFromText(plainText));
    handleAddTour({ ...formData, desc: htmlContent });
  };

  return (
    <Modal isOpen={isOpen} toggle={toggleModal} size="lg">
      <ModalHeader toggle={toggleModal}>Thm Tour</ModalHeader>
      <ModalBody>
        <Form onSubmit={handleSubmit}>
          <FormGroup>
            <Label for="title">Tiu </Label>
            <Input type="text" name="title" id="title" value={formData.title} onChange={handleChange} required />
          </FormGroup>
          <FormGroup>
            <Label for="city">Thnh ph</Label>
            <Input type="text" name="city" id="city" value={formData.city} onChange={handleChange} required />
          </FormGroup>
          <FormGroup>
            <Label for="address">a ch</Label>
            <Input type="text" name="address" id="address" value={formData.address} onChange={handleChange} required />
          </FormGroup>
          <FormGroup>
            <Label for="distance">Khong cch</Label>
            <Input type="number" name="distance" id="distance" value={formData.distance} onChange={handleChange} required />
          </FormGroup>
          <FormGroup>
            <Label for="photo">Hnh nh</Label>
            <Input type="text" name="photo" id="photo" value={formData.photo} onChange={handleChange} required />
          </FormGroup>
          <FormGroup>
            <Label for="desc">M t</Label>
            <Editor
              editorState={editorState}
              wrapperClassName="demo-wrapper"
              editorClassName="demo-editor"
              onEditorStateChange={(editorState) => setEditorState(editorState)}
            />
          </FormGroup>
          <FormGroup>
            <Label for="price">Gi</Label>
            <Input type="number" name="price" id="price" value={formData.price} onChange={handleChange} required />
          </FormGroup>
          <FormGroup>
            <Label for="maxGroupSize">S Ngi</Label>
            <Input type="number" name="maxGroupSize" id="maxGroupSize" value={formData.maxGroupSize} onChange={handleChange} required />
          </FormGroup>
          <FormGroup>
            <Label for="featured">Ni bt</Label>
            <Input type="select" name="featured" id="featured" value={formData.featured} onChange={handleChange} required>
              <option value="">Chn</option>
              <option value="true">C</option>
              <option value="false">Khng</option>
            </Input>
          </FormGroup>
          <Button type="submit" color="primary">Thm</Button>
          <Button color="secondary" onClick={toggleModal}>Hy</Button>
        </Form>
      </ModalBody>
    </Modal>
  );
};

export default AddTourModal;
