import React from "react";
import { Modal, ModalHeader, ModalBody, ModalFooter, Button, Row, Col } from "reactstrap";

const TourInfoModal = ({ open, handleClose, selectedTour }) => {
  return (
    <Modal isOpen={open} toggle={handleClose}>
      <ModalHeader toggle={handleClose}>Ton B Thng Tin Tour</ModalHeader>
      <ModalBody>
        {selectedTour && (
          <React.Fragment>
            <Row>
              <Col>
                <strong>S hiu tour:</strong> {selectedTour.id}
              </Col>
            </Row>

            <Row>
              <Col>
                <strong>Tn Chuyn i:</strong> {selectedTour.title}
              </Col>
            </Row>


            <Row>
              <Col>
                <strong>a Ch Tour:</strong> {selectedTour.address}
              </Col>
            </Row>
            <Row>
              <Col>
                <strong>Thnh Ph:</strong> {selectedTour.city}
              </Col>
            </Row>
            <Row>
              <Col>
                <strong>Khong Cch:</strong> {selectedTour.distance}
              </Col>
            </Row>
            <Row>
              <Col>
                <strong>S lng Ngi trong tour:</strong> {selectedTour.maxGroupSize}
              </Col>
            </Row>
            <Row>
              <Col>
                <strong>nh Tour:</strong>
              </Col>
            </Row>
            <Row>
              <Col>
                <img
                  src={selectedTour.photo}
                  alt={selectedTour.tourId}
                  style={{ maxWidth: "100%", height: "auto" }}
                />
              </Col>
            </Row>
          </React.Fragment>
        )}
      </ModalBody>
      <ModalFooter>
        <Button color="primary" onClick={handleClose}>
          ng
        </Button>{" "}
      </ModalFooter>
    </Modal>
  );
};

export default TourInfoModal;
