import React, { useState } from "react";
import { Table, Button, Spinner, Pagination, PaginationItem, PaginationLink  } from "reactstrap";
import "animate.css"; 
import { formatVND } from "../../utils/formatVND";

const itemsPerPage = 4; // S mc hin th trn mi trang
const BookingTable = ({
  bookings,
  loading,
  handleEditModalOpen,
  handleDeleteBooking,
  handleShowTourInfo,
}) => {


  const [currentPage, setCurrentPage] = useState(1);

  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentBookings = bookings.slice(indexOfFirstItem, indexOfLastItem);

  const paginate = (pageNumber) => setCurrentPage(pageNumber);

  if (loading) {
    return (
      <div className="text-center my-5">
        <Spinner color="primary" />
      </div>
    );
  }

  if (!Array.isArray(bookings) || bookings.length === 0) {
    return <p>Khng c n t hng no.</p>;
  }

  return (
    <div className="text-content">
      <Table striped bordered responsive className="booking-table"  >
        <thead>
          <tr className="border-orange">
            <th className="animate__animated animate__slideInUp" >ID</th>
            <th className="animate__animated animate__slideInUp">Tn khch hng</th>
            <th className="animate__animated animate__slideInUp">Email</th>
            <th className="animate__animated animate__slideInUp">S in thoi</th>
            <th className="animate__animated animate__slideInUp">a ch</th>
            <th className="animate__animated animate__slideInUp">Ghi ch</th>
            <th className="animate__animated animate__slideInUp">S lng khch</th>
            <th className="animate__animated animate__slideInUp">ngy t tour</th>
            <th className="animate__animated animate__slideInUp">s tin thanh ton </th>
            <th className="animate__animated animate__slideInUp">Tn Tour  booking</th>
            <th className="animate__animated animate__slideInUp">V Tour</th>
            <th className="animate__animated animate__slideInUp">trng thi thanh ton</th>
            <th className="animate__animated animate__slideInUp">Thao tc</th>
          </tr>
        </thead>
        <tbody>
          {currentBookings.map((booking) => (
            <tr key={booking.bookingId} className="border-orange animate__animated animate__slideInUp">
              <td>{booking.bookingId}</td>
              <td>{booking.fullName}</td>
              <td>{booking.email}</td>
              <td>{booking.phoneNumber}</td>
              <td>{booking.address}</td>
              <td>{booking.note}</td>
              <td>{booking.guestSize}</td>
              <td>{booking.bookingAt}</td>
              <td>{formatVND(booking.totalAmount)}</td>
              <td>{booking.titleTour}</td>
              
              <td>
                <Button color="primary" onClick={() => handleShowTourInfo(booking.tour)}>
                  Xem thng tin
                </Button>
              </td>
              <td>{booking.status}</td>
              <td>
                <Button color="primary" onClick={() => handleEditModalOpen(booking)}>
                  Sa
                </Button>{" "}
                <Button color="danger" onClick={() => handleDeleteBooking(booking.bookingId)}>
                  Xa
                </Button>
              </td>
            </tr>
          ))}
        </tbody>
      </Table>
      <Pagination className="pagination1">
        {Array.from({ length: Math.ceil(bookings.length / itemsPerPage) }).map((_, index) => (
          <PaginationItem key={index} active={index + 1 === currentPage}>
            <PaginationLink onClick={() => paginate(index + 1)}>{index + 1}</PaginationLink>
          </PaginationItem>
        ))}
      </Pagination>
    </div>
  );
};

export default BookingTable;
