import React, { useState, useEffect, useContext } from "react";
import {
  Table,
  Button,
  Input,
  FormGroup,
  Container,
  Row,
  Col,
  Pagination,
  PaginationItem,
  PaginationLink,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
} from "reactstrap";
import "./../../styles/user-List.css"
import AddUserModal from "./AddUserModal";
import EditUserModal from "./EditUserModal";
import { AuthContext } from "../../../context/AuthContext";
import { useNavigate } from "react-router-dom";
import "animate.css";

const ITEMS_PER_PAGE = 8;
const BASE_URL_AUTH = "http://localhost:8888/api/users";

const UserList = () => {
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const [page, setPage] = useState(1);
  const [pageSize, setPageSize] = useState(ITEMS_PER_PAGE);
  const [totalPages, setTotalPages] = useState();
  const [searchText, setSearchText] = useState("");
  const [openAddModal, setOpenAddModal] = useState(false);
  const [openEditModal, setOpenEditModal] = useState(false);
  const [userToEdit, setUserToEdit] = useState(null);
  const [confirmModalOpen, setConfirmModalOpen] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    fetchUsers();
    window.scrollTo({ top: 0, behavior: "smooth" });
  }, [page, pageSize]);

  const { token } = useContext(AuthContext);

  const fetchUsers = () => {
    setLoading(true);

    fetch(`${BASE_URL_AUTH}/search`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify({
        pageNumber: page,
        pageSize: pageSize,
        username: searchText,
        role: null,
        sortBy: "userId",
        sortType: "ASC",
        email: null,
        fullName: null,
        phone: null,
      }),
    })
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error("Khng th ly danh sch ngi dng.");
        }
      })
      .then((data) => {
        setUsers(data.content);
        setTotalPages(data.totalPages);
      })
      .catch((error) => {
        console.error(error);
        setError("C li xy ra khi ly danh sch ngi dng.");
      })
      .finally(() => {
        setLoading(false);
      });
  };

  const handleSearchTextChange = (event) => {
    setSearchText(event.target.value);
  };

  const handleSearch = () => {
    setPage(1);
    fetchUsers();
  };

  const handlePageChange = (event, newPage) => {
    setPage(newPage);
  };

  const handleDeleteUser = (userId) => {
    setConfirmModalOpen(true);
    setUserToEdit(userId);
  };

  const handleConfirmDelete = () => {
    fetch(`${BASE_URL_AUTH}/${userToEdit}`, {
      method: "DELETE",
      headers: {
        Authorization: `Bearer ${token}`,
      },
    })
      .then((response) => {
        if (response.ok) {
          alert(" xa thnh cng");
          fetchUsers();
        } else {
          throw new Error("Khng th xa ngi dng.");
        }
      })
      .catch((error) => {
        console.error(error);
        setError("C li xy ra khi xa ngi dng.");
      })
      .finally(() => {
        setConfirmModalOpen(false);
        setUserToEdit(null);
      });
  };

  const handleCancelDelete = () => {
    setConfirmModalOpen(false);
    setUserToEdit(null);
  };

  const handleAddUser = (newUser) => {
    fetch(`${BASE_URL_AUTH}/create`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify(newUser),
    })
      .then((response) => {
        if (response.ok) {
          fetchUsers();
          alert("Thm mi thnh cng");
        } else {
          throw new Error("Khng th thm mi ngi dng.");
        }
      })
      .catch((error) => {
        console.error(error);
        setError("C li xy ra khi thm mi ngi dng.");
        navigate("/admin");
      })
      .finally(() => {
        setOpenAddModal(false);
      });
  };

  const handleEditUser = (updatedUser) => {
    fetch(`${BASE_URL_AUTH}/edit/${updatedUser.userId}`, {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify(updatedUser),
    })
      .then((response) => {
        if (response.ok) {
          alert(" chnh sa thnh cng");
          fetchUsers();
        } else {
          fetchUsers();
          throw new Error("Khng th cp nht ngi dng.");
        }
      })
      .catch((error) => {
        navigate("/admin");
        setError("C li xy ra khi cp nht ngi dng.");
      })
      .finally(() => {
        setOpenEditModal(false);
        fetchUsers();
      });
  };

  const handleEditModalOpen = (user) => {
    setUserToEdit(user);
    setOpenEditModal(true);
  };

  return (
    <div className="content">
      <Container>
        <h1 className="text-center">Danh sch ngi dng</h1>
        <Row>
          <Col xs={12} md={4}>
            <FormGroup>
              <Input
                type="text"
                placeholder="Tm kim theo tn"
                value={searchText}
                onChange={handleSearchTextChange}
              />
            </FormGroup>
          </Col>
          <Col xs={12} md={4}>
            <Button color="primary" onClick={handleSearch} block>
              Tm kim
            </Button>
          </Col>
          <Col xs={12} md={4}>
            <Button
              color="primary"
              onClick={() => setOpenAddModal(true)}
              block
              style={{ marginTop: "10px" }}
            >
              Thm mi
            </Button>
          </Col>
        </Row>
      </Container>

      {loading ? (
        <div className="text-center">
          <h5>Loading...</h5>
        </div>
      ) : error ? (
        <div className="text-center">
          <h5 className="text-danger">{error}</h5>
        </div>
      ) : (
        <Container className="content-wrapper animate__animated animate__slideInUp">
          <Table striped bordered responsive>
            <thead>
              <tr className="border-orange animate__animated animate__slideInUp">
                <th>ID</th>
                <th>Tn ng nhp</th>
                <th>Email</th>
                <th>H v tn</th>
                <th>in thoi</th>
                <th>Vai tr</th>
                <th>Thao tc</th>
              </tr>
            </thead>
            <tbody>
              {users.map((user) => (
                <tr key={user.userId} className="border-orange animate__animated animate__slideInUp">
                  <td>{user.userId}</td>
                  <td>{user.username}</td>
                  <td>{user.email}</td>
                  <td>{user.fullName}</td>
                  <td>{user.phone}</td>
                  <td>{user.role}</td>
                  <td>
                    <Button
                      color="danger"
                      onClick={() => handleDeleteUser(user.userId)}
                    >
                      Xa
                    </Button>
                    <Button
                      color="primary"
                      onClick={() => handleEditModalOpen(user)}
                    >
                      Sa
                    </Button>
                  </td>
                </tr>
              ))}
            </tbody>
          </Table>
          <div>
      <Pagination   className="pagination1">
        {[...Array(totalPages).keys()].map((pageIndex) => (
          <PaginationItem key={pageIndex} active={pageIndex + 1 === page}>
            <PaginationLink onClick={() => handlePageChange(null, pageIndex + 1)}>
              {pageIndex + 1}
            </PaginationLink>
          </PaginationItem>
        ))}
      </Pagination>
    </div>
        </Container>
      )}

      <AddUserModal
        open={openAddModal}
        onClose={() => setOpenAddModal(false)}
        onAddUser={handleAddUser}
      />

      {userToEdit && (
        <EditUserModal
          open={openEditModal}
          onClose={() => setOpenEditModal(false)}
          userToEdit={userToEdit}
          onUpdateUser={handleEditUser}
        />
      )}

      {/* Confirm delete modal */}
      <Modal isOpen={confirmModalOpen} toggle={handleCancelDelete}>
        <ModalHeader toggle={handleCancelDelete}>Xc nhn xa</ModalHeader>
        <ModalBody>Bn c chc chn mun xa ngi dng ny?</ModalBody>
        <ModalFooter>
          <Button color="secondary" onClick={handleCancelDelete}>
            Thot
          </Button>
          <Button color="danger" onClick={handleConfirmDelete}>
            Xa
          </Button>
        </ModalFooter>
      </Modal>
    </div>
  );
};

export default UserList;
