import React, { useState, useEffect } from "react";
import "./booking.css";
import { Form, FormGroup, ListGroup, ListGroupItem, Button } from "reactstrap";
import { useNavigate } from "react-router-dom";
import { BASE_URL } from "../../utils/config";
import axios from "axios"; 



const formatVND = (amount) => {
  if (amount === undefined || isNaN(amount)) {
    return '0'; // Hoc gi tr mc nh khc ty 
  }
  return amount.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.');
};


const Booking = ({ tour, avgRating }) => {
  // Ly ra gi v nh gi trung bnh t prop tour
  const { price, reviews } = tour;

  const [isExceedMaxGroupSize, setIsExceedMaxGroupSize] = useState(false);


  // S dng hook useNavigate ca React Router  iu hng khi t tour thnh cng
  const navigate = useNavigate();

  // Khi to state booking cha thng tin t tour ca ngi dng
  const [booking, setBooking] = useState({
    email: "",
    fullName: "",
    phoneNumber: "",
    address: "",
    note: "",
    guestSize: 1,
    tour: tour.title,
    tour: tour.id,
    totalAmount :0,
  });

  
  // Khi to state isFormValid  kim tra tnh hp l ca biu mu
  const [isFormValid, setIsFormValid] = useState(false);

  const formattedPrice = formatVND(price);

  // Khi to state formError  lu tr thng bo li cho tng trng
  const [formError, setFormError] = useState({
    email: "",
    fullName: "",
    phoneNumber: "",
    address: "",
    note: "",
    guestSize: "",
  });

  // useEffect s c gi li mi khi d liu biu mu thay i
  

  // Hm handleChange  cp nht state booking khi ngi dng thay i thng tin
  const handleChange = (e) => {
    const { id, value } = e.target;
    setBooking((prev) => ({ ...prev, [id]: value }));
    // Tnh ton li v cp nht totalAmount khi c thay i trong biu mu
  const newTotalAmount =
  Number(price) * Number(value) + Number(serviceFee);
  setBooking((prev) => ({ ...prev, totalAmount: newTotalAmount }));

  };

  // Tnh ph dch v v tng s tin cn thanh ton
  const serviceFee = 20000;
  const totalAmount = Number(price) * Number(booking.guestSize) + Number(serviceFee);
  const formatteTotalAmount = formatVND(totalAmount);
  const formatteserviceFee = formatVND(serviceFee);
  useEffect(() => {
    // Kim tra thng bo li cho tng trng
    const { email, fullName, phoneNumber, address, guestSize } = booking;
    const errors = {};

    if (email.trim() === "") {
      errors.email = "Vui lng nhp email";
    }
    if (fullName.trim() === "") {
      errors.fullName = "Vui lng nhp tn y ";
    }
    if (phoneNumber.trim() === "") {
      errors.phoneNumber = "Vui lng nhp s in thoi";
    }
    if (address.trim() === "") {
      errors.address = "Vui lng nhp a ch";
    }
  
    if (Number(guestSize) <= 0) {
      errors.guestSize = "S ngi phi ln hn 0";
    }

    // Cp nht gi tr cho bin isFormValid da trn kt qu kim tra
    setIsFormValid(Object.values(errors).every((val) => val === ""));

    // Cp nht state formError vi thng bo li cho tng trng
    setFormError(errors);
  }, [booking]);
  // Hm x l khi ngi dng nhn nt "t Ngay"
  const handleClick = async (e) => {
    e.preventDefault();

     // Kim tra nu guestSize vt qu maxGroupSize
  if (booking.guestSize > tour.maxGroupSize) {
    setIsExceedMaxGroupSize(true);
    return alert("s lng ngi vt qu s lng ngi trong tour"); // Dng tin trnh nu vt qu
  }
  
    const totalAmountAsNumber = parseFloat(formatteTotalAmount.replace(/\./g, ''));
    const updatedBooking = { ...booking, totalAmount: totalAmountAsNumber };
    try {
      // Gi d liu t tour ln my ch thng qua API
      const res = await axios.post(`${BASE_URL}/bookings/create`, updatedBooking, {
        headers: {
          "content-type": "application/json",
        },
      });

      // Nu t tour thnh cng, iu hng ti trang "thank-you"
      if (res.status >= 200 && res.status < 300) {
        navigate("/thank-you");
      }
    } catch (err) {
   
      alert(err.message);
    }
  };

  return (
    <div className="booking">
      {/* Hin th gi v nh gi */}
      <div className="booking__top d-flex align-items-center justify-content-between">
        <h3>
          {formattedPrice} VND
          <span>/Ngi</span>
        </h3>
        <span className="tour__rating d-flex align-item-center">
          <i className="ri-star-s-fill"></i>
          {avgRating === 0 ? null : avgRating} ({reviews?.length})
        </span>
      </div>

      {/* Biu mu t tour */}
      <div className="booking__form">
        <h5>Thng Tin C Nhn</h5>
        <Form className="booking__info-form" onSubmit={handleClick}>
          <FormGroup>
          <p style={{ color: "red", textAlign: "right" }}>*</p>
            <input
              type="text"
              placeholder="email"
              id="email"
              value={booking.email}
              required
              onChange={handleChange}
            />
            <div className="error-message">{formError.email}</div>
          </FormGroup>
          <FormGroup>
        <p style={{ color: "red", textAlign: "right" }}>*</p>
            <input
              type="text"
              placeholder="Tn y "
              id="fullName"
              value={booking.fullName}
              required
              onChange={handleChange}
            />
            <div className="error-message">{formError.fullName}</div>
          </FormGroup>
          <FormGroup>
        <p style={{ color: "red", textAlign: "right" }}>*</p>
            <input
              type="text"
              placeholder="a Ch"
              id="address"
              value={booking.address}
              required
              onChange={handleChange}
            />
            <div className="error-message">{formError.address}</div>
          </FormGroup>
          <p style={{ color: "red" }}>Thng Tin Thm Nu C </p>
          <FormGroup>
            <input
              type="text"
              placeholder="Thng Tin Thm"
              id="note"
              value={booking.note}
              required
              onChange={handleChange}
            />
           
          </FormGroup>
          <FormGroup>
          <p style={{ color: "red", textAlign: "right" }}>*</p>
            <input
              type="text"
              placeholder="S in Thoi"
              id="phoneNumber"
              value={booking.phoneNumber}
              required
              onChange={handleChange}
            />
            <div className="error-message" >{formError.phoneNumber}</div>
          </FormGroup>
          <p style={{ color: "red", textAlign: "right" }}>*</p>
          <FormGroup className="d-flex align-items-center gap-3">
         
            <input
              type="number"
              placeholder="S ngi"
              id="guestSize"
              value={booking.guestSize}
              required
              onChange={handleChange}
            />
            <div className="error-message">{formError.guestSize}</div>
          </FormGroup>
        </Form>

        
      </div>

      {/* Thng tin n hng */}
      <div className="booking__bottom">
        <ListGroup>
          <ListGroupItem className="border-0 px-0">
            <h5 className="d-flex align-items-center gap-1">
            {formattedPrice}<i className="ri-close-line"> Ngi</i>{" "}
            </h5>
            <span> {formattedPrice} VND</span>
          </ListGroupItem>
          <ListGroupItem className="border-0 px-0">
            <h5>Thu</h5>
            <span>{formatteserviceFee} VND</span>
          </ListGroupItem>
          <ListGroupItem className="border-0 px-0 total">
            <h5>Tng</h5>
            <span>{formatteTotalAmount} VND</span>
          </ListGroupItem>
        </ListGroup>
        {/* Nt t Ngay */}
        <Button
          className="btn primary__btn w-100 mt-4"
          onClick={handleClick}
          disabled={!isFormValid}
        >
          t Ngay
        </Button>
      </div>
    </div>
  );
};

// Xut thnh phn Booking  s dng trong cc thnh phn khc
export default Booking;
